import {
  Component
} from '@angular/core';
import {
  ActionSheetController, PopoverController
} from '@ionic/angular';
import {
  Router,
  ActivatedRoute
} from '@angular/router';
import {
  UserService
} from '../services/user.service';
import { AlertController } from '@ionic/angular';
import { PopUpUserComponent } from '../messages/pop-up-user/pop-up-user.component';

@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {
  users: any = [];
  isLoading: boolean = false;
  searchText: string = '';
  
  constructor(
    public actionSheetController: ActionSheetController,
    private router: Router,
    public popoverController: PopoverController,
    private route: ActivatedRoute,
    public alertController: AlertController,
    private userService: UserService
  ) {}

  ngOnInit() {
    // console.log('wow');
  }
  
  ionViewWillEnter() {
    this.isLoading = true;
    this.searchContacts();
  }

  searchContacts() {
    this.isLoading = true;
    console.log(this.searchText);
    this.userService.getUsers({searchText: this.searchText})
    .subscribe(
      (res:any) => {
        this.isLoading = false;
        console.log(res);
        this.users = res;
      }
    )
  }

  async showUserPopup(contact: any) {
    const popover = await this.popoverController.create({
      component: PopUpUserComponent,
      // event: ev,
      animated: true,
      showBackdrop: true,
      componentProps: {
        contact
      }
    });
    return await popover.present();
  }
  async presentContactInfo(message: any, header: any) {
    const alert = await this.alertController.create({
      header,
      message,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'success',
          handler: (blah) => {
            // console.log('Confirm Cancel: blah');
          }
        }
      ]
    });
    await alert.present();
  }

  showInfo(contact: any) {
    this.presentContactInfo(`Phone: <a href="tel:${contact.phone_no}">${contact.phone_no || ' - '}</a> <br> <br>Email: <a href="mailto:${contact.email}">${contact.email}</a>`, `${contact.fname} ${contact.lname}`);
  }

  getUsers() {
    this.isLoading = true;

    this.userService.getUsers({})
      .subscribe(o => {
        this.isLoading = false;
        this.users = o;
        // console.log(o, '0');
      })
  }
}